
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'

import api from '../../assets/js/api'
export default {
components: {
XInput,
Group,
Countdown
},
data () {
return {
name: '',
pwd: '',
phone: '',
code: '',
password: '',
togglePwd: true,
step: 1,
time: 90,
start: true,
isSendCoding: false
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(text){
this.$vux.loading.show({
text: text || '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
verPhoneStep(){
if (!this.$refs.phoneInput.valid) {
this.layer('请输入正确的手机号')
} else if (!this.$refs.codeInput.valid) {
this.layer('请输入验证码')
} else {
this.showLoading('验证中')
this.$http.post(api.checkCode, {
event: 'resetpwd',
mobile: this.phone,
captcha: this.code
}).then(res => {
this.hideLoading();
if (res.code == 1) {
this.step = 2;
}
}).catch(e => {
this.hideLoading();
})
}
},
pwdSubmit(){
if (!this.$refs.pwdInput.valid) {
this.layer('请输入密码')
} else {
this.showLoading('修改中')
this.$http.post(api.resetpwd, {
type: 'mobile',
mobile: this.phone,
newpassword: this.password,
captcha: this.code
}).then(res => {
if (res.code == 1) {
this.step = 3;
}
})
}
},
finish(){
this.isSendCoding = false;
this.time = 90;
},
sendCode(){
if (!this.$refs.phoneInput.valid) {
this.layer('请输入正确的手机号')
} else {
this.$http.post(api.getCode, {
event: 'resetpwd',
mobile: this.phone
}).then(res => {
if (res.code == 1) {
this.isSendCoding = true;
}
})
}
}
},
mounted(){
document.getElementsByTagName('title')[0].textContent = '忘记密码';
}
}



