
import XInput from 'vux/src/components/x-input/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

import api from '../../assets/js/api'
export default {
components: {
XInput,
},
data () {
return {
/* 18520654081 qwe123 */
account: '',
password: ''
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(text){
this.$vux.loading.show({
text: text || '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
login(){
if (!this.$refs.phoneInput.valid) {
this.layer('请输入正确的手机号')
} else if (!this.$refs.pwdInput.valid) {
this.layer('请输入密码')
} else {
this.showLoading('登录中')
this.$http.post(api.login, {
account: this.account,
password: this.password
}).then(res => {
this.hideLoading();
if (res.code == 1) {
if (res.data.userinfo.token) {

cookie.set('token', res.data.userinfo.token, {
expires: 7
});

this.$router.replace({
path: '/meIndex'
})
}
}
}).catch(e => {
this.hideLoading();
})
}
},
gotoForgetPwd(){
this.$router.push({
path: '/forgetPwd'
})
}
},
mounted() {

document.getElementsByTagName('title')[0].textContent = '登录';
}
}



