import api from '../../assets/js/api'
export default {
    components: {},
    data () {
        return {
            userInfo: {
                "user_id": "",
                "username": "",
                "nickname": "",
                "mobile": "",
                "avatar": "",
                "identity": "",
                "wx_name": ""
            },
            identity: {
                'store': '创客空间',
                'director': '创客主管',
                'member': '创客'
            },
            account: {
                balance: 0,
                frost: 0
            }
        }
    },
    methods: {
        layer(text){
            this.$vux.toast.text(text || 'hello', 'middle')
        },
        showLoading(){
            this.$vux.loading.show({
                text: '加载中'
            })
        },
        hideLoading(){
            this.$vux.loading.hide()
        },
        gotoPage(page){
            this.$router.push({
                path: page
            })
        },
        getUserInfo(){
            this.$http.post(api.getUserInfo)
                .then(res => {
                    if (res.code == 1) {
                        this.userInfo = res.data.userinfo;
                        this.account = res.data.account;
                    }
                })
        }
    },
    mounted() {
        this.getUserInfo();
        document.getElementsByTagName('title')[0].textContent = '个人中心';
    }
}



